import React from 'react'
import { Link as RLink } from "react-router-dom";

const Menu = ({ listOrientation }) => {
    return (
        <ul className={`menu ${listOrientation}`}>
            {
                [
                    {
                        route: '/',
                        name: 'Home'
                    },
                    {
                        route: '/menu',
                        name: 'Menu'
                    },
                    {
                        route: '/about',
                        name: 'About'
                    },
                    {
                        route: '/contact',
                        name: 'Contact'
                    },
                ].map(el => 
                    <li>
                        <RLink to={el.route}>
                            { el.name }
                        </RLink>
                    </li>
                )
            }
        </ul>
    )
}

export default Menu
